@import '../Helpers/Helpers.scss';

.questions {
    // background-color: $purple-dark;
    // margin: 0;
    // padding: 10px 10px 0 10px;

    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;


    &__list {
        margin: 0;
        width: 85%;
        padding: 10px 10px 0 10px;
        // width: max-content;
        border-radius: 10px;
        height: 100%;
        max-height: 38vh;
        overflow: auto;

        @include medium() {
            width: 90%;
        }

        > p {
            margin: 0;
            font-size: 8px;
            border-bottom: 1px solid $purple-lite;
            padding: 5px;
    
            @include medium() {
                font-size: 10px;
            }
    
            @include large() {
                font-size: 12px;
            }
        }
    }
}