@import '../../Helpers/Helpers.scss';

.gameStart {
  margin: 0 auto;
  text-align: center;

  &__message {
    margin: 0;
    font-family: $shrift--rubic;
    letter-spacing: 1px;
    font-size: 18px;
  }

  &__wrapper {
    animation: shaker 3s ease-in infinite;
    >i {
        font-size: 100px;
    }
  }

  &__arrow {
    position: relative;
    &-mark {
      position: absolute;
      font-size: 40px;
      font-family: $shrift--ubuntu;
      font-weight: 700;
      color: #fff;
      right: 41px;
      top: 25px;
    }
  }
}

@keyframes shaker {
  0% {
    transform: translateY(0);
  }

    50% {
    transform: translateY(70%);
    }

    100% {
    transform: translateY(0);

    }
}