.Loader {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

  &__content {
    border-radius: 50%;
    width: 2em;
    height: 2em;
    margin: 1em auto;
    border: 0.3em solid #ddd;
    border-left-color: #000;
    animation: load8 1.2s infinite linear;

    &--small {
      border-radius: 50%;
      width: 1em;
      height: 1em;
      margin: 0;
      // border: 0.1em solid #ddd;
      // border-left-color: #000;
      animation: load8 1.2s infinite linear;
    }
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
