@import '../Helpers/Helpers.scss';

.errorMessage {
  color: rgba($color: $red, $alpha: .6);
  font-size: 6px;
  // width: 45%;
  margin: 0;
  margin-top: 3.5px;
  // position: absolute;
  font-family: $shrift--ubuntu;

  @include medium() {
    font-size: 7px;
  }

  @include large() {
    font-size: 9px;
  }

  &__big {
    font-size: 40px;
    font-weight: 700;
    margin: 0 auto;
  }
}
